import React, { Fragment, useState, useEffect } from 'react';

import differenceInCalendarMonths from 'date-fns/difference_in_calendar_months';
import addMonths from 'date-fns/add_months';
import getMonth from 'date-fns/get_month';
import getYear from 'date-fns/get_year';
import isWithinRange from 'date-fns/is_within_range';
import parse from 'date-fns/parse';
import lastDayOfMonth from 'date-fns/last_day_of_month';

import {
  determineAlreadySelectedTripDates,
  getMonthsInRange
} from '../helpers/dateUtils';

import Spinner from './Spinner.react';
import DateRange from './DateRange.react';
import LocationMap from './LocationMap.react';
import TripCard from './TripCard.react';
import NewTrip from './NewTrip.react';

// Ugh...
const MONTH_NAMES = [
  'January',
  'February',
  'March',
  'April',
  'May',
  'June',
  'July',
  'August',
  'September',
  'October',
  'November',
  'December'
].map(m => m.toUpperCase());

const TripList = ({ user, activeUser, locations, isFetching }) => {
  //
  // State for component
  //
  const [tripSelection, setSelectedTrip] = useState(null);
  const [dateSelection, setDateSlider] = useState([
    getMonth(new Date()),
    getYear(new Date())
  ]);

  //
  // Make sure the date selection is still valid after changes to
  // the locations list
  //
  useEffect(
    () => {
      if (locations.length > 0) {
        setDateSlider([
          getMonth(locations[0].start_date),
          getYear(locations[0].start_date)
        ]);
      } else {
        setDateSlider([getMonth(new Date()), getYear(new Date())]);
      }
    },
    [getMonthsInRange(locations)]
  );

  //
  // Loading states
  //
  if (!locations || isFetching) {
    return <Spinner />;
  }
  if (locations.length === 0) {
    return (
      <div>
        No trips yet...
        <br />
        {user.id === activeUser && (
          <Fragment>
            <hr />
            <h4>ADD NEW TRIP</h4>
            <NewTrip trips={locations} />
          </Fragment>
        )}
      </div>
    );
  }

  // Only show locations from selected month's trips (unless there is only 1 trip)
  const filteredLocations =
    locations.length > 1
      ? locations.filter(isWithinMonth(dateSelection))
      : locations;

  const tripListTitleText =
    locations.length > 1
      ? `TRIPS IN ${MONTH_NAMES[dateSelection[0]]}`
      : 'TRIPS';
  // Disable adding dates that overlap with existing trips
  const disabledDates = determineAlreadySelectedTripDates(locations);
  const activeTrip = filteredLocations.find(trip => trip.id === tripSelection);

  return (
    <Fragment>
      {activeTrip && (
        <TripCard
          disabledDates={disabledDates}
          {...activeTrip}
          viewOnly={user.id !== activeUser}
        />
      )}
      {!activeTrip && <TripCard />}
      <LocationMap locations={filteredLocations} onSelect={setSelectedTrip} />
      {getMonthsInRange(locations) > 0 && (
        <Fragment>
          <div className="uk-flex uk-margin">
            <input
              style={{ width: 200, margin: 'auto' }}
              className="uk-range uk-flex-around"
              type="range"
              onChange={e =>
                setDateSlider(dateFromOffset(e.target.value, locations))
              }
              max={getMonthsInRange(locations)}
              value={offsetFromDate(dateSelection, locations)}
            />
          </div>
          <label style={{ margin: 'auto' }}>
            <DateRange
              className="uk-flex-around"
              hideDaysCount
              start_date={toStartAndEndOfMonth(dateSelection)[0]}
              end_date={toStartAndEndOfMonth(dateSelection)[1]}
            />
          </label>
        </Fragment>
      )}

      {user.id === activeUser && (
        <Fragment>
          <hr />
          <h4>ADD NEW TRIP</h4>
          <NewTrip trips={locations} />
        </Fragment>
      )}

      <hr />

      <h4>{tripListTitleText}</h4>
      {filteredLocations.map(trip => (
        <TripCard
          key={trip.id}
          disabledDates={disabledDates}
          {...trip}
          viewOnly={user.id !== activeUser}
        />
      ))}
    </Fragment>
  );
};

//
// Helper Functions for converting to/from our [MONTH, YEAR] format
//
const offsetFromDate = (dateSelection, locations) => {
  const startOfMonth = toStartAndEndOfMonth(dateSelection)[0];
  return Math.abs(
    differenceInCalendarMonths(startOfMonth, locations[0].start_date)
  );
};

const dateFromOffset = (monthsFromStart, locations) => {
  const date = addMonths(locations[0].start_date, monthsFromStart);
  return [getMonth(date), getYear(date)];
};

const isWithinMonth = dateSelection => ({ start_date, end_date }) => {
  const [startOfMonth, endOfMonth] = toStartAndEndOfMonth(dateSelection);

  return (
    isWithinRange(start_date, startOfMonth, endOfMonth) ||
    isWithinRange(end_date, startOfMonth, endOfMonth)
  );
};

export const toStartAndEndOfMonth = ([month, year]) => [
  parse(`${year}-${month + 1}-01`),
  lastDayOfMonth(parse(`${year}-${month + 1}-01`))
];

export default TripList;
